import React from 'react';

import "./TagsContainer"
import "./../../styles/post-preview.css"
import TagsContainer from './TagsContainer';
import orange from "./../../media/images/orange.jpeg"

class PostPreview extends React.Component {
    render() {
        return (
            <div className="post-preview">
                <div className="post-grid">
                    <div className="post-text">
                        <a className="post-title" href={this.props.link}>{this.props.title}</a>
                        <div className="date">{this.props.date}</div>
                        <div className="snippet">{this.props.snippit}</div>
                        <TagsContainer tags={this.props.tags}></TagsContainer>
                    </div>
                    <div className="post-image">
                        <img src={orange}></img>
                    </div>
                </div>
            </div>
        );
    }
}

export default PostPreview;